.app-wrapper {
    display: grid;
    width: 1200px;
    grid-gap: 5px;
    margin: 0px;


    grid-template-areas: "h h" "n c";


    grid-template-rows: 60px 1fr;
    grid-template-columns: 200px 10fr;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #75bde7;
}


